___CSS_LOADER_EXPORT___.push([module.id, `html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background: linear-gradient(#3035c3, #a3c5ef);
  min-height: 100vh;
  display: grid;
  gap: 10px;
  align-content: start;
  justify-content: center;

  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  color: white;
}

h1 {
  color: white;
  font-size: 2.7rem;
  text-align: center;
  padding-top: 20px;
}

form {
  justify-self: center;
  background-color: #173675;
  padding: 15px 20px;

  display: grid;
  grid-template-columns: 1fr auto;
  gap: 5px;

  align-content: center;
  border-radius: 10px;
}

h2 {
  grid-column: 1 / 3;
  color: white;
  margin: 0;
  font-size: 1.5rem;
}

label {
  display: flex;
  align-items: center;
}

input {
  width: 250px;
  outline: black 6px;
  padding: 0 10px;
  font-size: 1rem;
  font-weight: bold;
}

button {
  background-color: #173675;
  border: none;
  padding: 5px;
  border-radius: 3px;
}

button:hover {
  background-color: #072155;
}

button:active {
  background-color: #173675;
}

label svg {
  fill: white;
}

.message-to-user {
  visibility: hidden;
  min-height: 40px;
  width: fit-content;

  color: rgb(255, 255, 255);
  background-color: #d2e0f18e;
  padding: 4px 8px;
  border-radius: 3px;

  text-align: center;
  justify-self: center;
}

.temperature-toggler {
  position: absolute;
  top: 10px;
  right: 10px;
  margin: 0;

  display: grid;
  grid-template-columns: auto auto;
}
.temperature-toggler button {
  color: white;
  padding: 5px 15px;
  margin: 0;
  font-weight: bold;
  background-color: rgb(101, 104, 114);
}

.temperature-toggler button:hover {
  background-color: rgb(119, 120, 121);
}

.temperature-toggler button:active {
  background-color: rgb(101, 104, 114);
}

.temperature-toggler .celsius {
  border-radius: 8px 0 0 8px;
}

.temperature-toggler .fahrengheit {
  border-radius: 0 8px 8px 0;
}

.temperature-toggler button.active {
  background-color: #0a2355;
}

.weather-data-container {
  display: grid;
  gap: 50px;
  margin: 0 20px 50px;
}

.current-forecast {
  background-color: #4955c2;
  padding: 15px;
  border-radius: 15px;
  width: fit-content;
  justify-self: center;

  display: grid;
  gap: 10px;
}

.current-forecast .location-container {
  display: flex;
  gap: 10px;
}

.current-forecast .location-container svg {
  fill: white;
}

.weather-container {
  display: grid;
  gap: 0px;
}

.current-forecast .weather-condition-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;

  font-size: 2rem;
  font-weight: 600;
}

.current-forecast .weather-description,
.current-forecast .weather-feelslike {
  display: flex;
  justify-content: center;
}

.current-forecast .weather-description {
  margin-bottom: 15px;
}

.forecast-container {
  display: grid;
  justify-items: center;
  gap: 20px;
}

.forecast-cards-container {
  min-width: 90vw;
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  gap: 20px;
  justify-content: center;
}

.weather-card {
  width: 100%;
  background-color: #4955c2;
  padding: 15px;
  border-radius: 15px;

  display: grid;
  justify-content: center;
  gap: 5px;

  justify-self: center;
}

.weather-card .date {
  text-align: center;
  font-weight: 600;
}

.weather-card img {
  justify-self: center;
}

.weather-card .description {
  margin-bottom: 15px;
  text-align: center;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;AACxB;;AAEA;;;EAGE,mBAAmB;AACrB;;AAEA;EACE,6CAA6C;EAC7C,iBAAiB;EACjB,aAAa;EACb,SAAS;EACT,oBAAoB;EACpB,uBAAuB;;EAEvB,eAAe;EACf,yCAAyC;EACzC,YAAY;AACd;;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,iBAAiB;AACnB;;AAEA;EACE,oBAAoB;EACpB,yBAAyB;EACzB,kBAAkB;;EAElB,aAAa;EACb,+BAA+B;EAC/B,QAAQ;;EAER,qBAAqB;EACrB,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,SAAS;EACT,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,kBAAkB;EAClB,eAAe;EACf,eAAe;EACf,iBAAiB;AACnB;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,kBAAkB;;EAElB,yBAAyB;EACzB,2BAA2B;EAC3B,gBAAgB;EAChB,kBAAkB;;EAElB,kBAAkB;EAClB,oBAAoB;AACtB;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,WAAW;EACX,SAAS;;EAET,aAAa;EACb,gCAAgC;AAClC;AACA;EACE,YAAY;EACZ,iBAAiB;EACjB,SAAS;EACT,iBAAiB;EACjB,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,SAAS;EACT,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,aAAa;EACb,mBAAmB;EACnB,kBAAkB;EAClB,oBAAoB;;EAEpB,aAAa;EACb,SAAS;AACX;;AAEA;EACE,aAAa;EACb,SAAS;AACX;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,aAAa;EACb,QAAQ;AACV;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,SAAS;;EAET,eAAe;EACf,gBAAgB;AAClB;;AAEA;;EAEE,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,SAAS;AACX;;AAEA;EACE,eAAe;EACf,aAAa;EACb,8CAA8C;EAC9C,SAAS;EACT,uBAAuB;AACzB;;AAEA;EACE,WAAW;EACX,yBAAyB;EACzB,aAAa;EACb,mBAAmB;;EAEnB,aAAa;EACb,uBAAuB;EACvB,QAAQ;;EAER,oBAAoB;AACtB;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,mBAAmB;EACnB,kBAAkB;AACpB","sourcesContent":["html {\n  box-sizing: border-box;\n}\n\n*,\n*::before,\n*::after {\n  box-sizing: inherit;\n}\n\nbody {\n  background: linear-gradient(#3035c3, #a3c5ef);\n  min-height: 100vh;\n  display: grid;\n  gap: 10